<template>
  <div>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="darkred">
      <van-swipe-item v-for="item in gallery" :key="item.id">
        <!-- <img :src="item.img_url" width="100%" alt="" /> -->
        <!-- 懒加载 -->
        <img v-lazy="item.img_url" width="100%" />
      </van-swipe-item>
    </van-swipe>
    <Tips />
    <!-- 产品信息渲染和cell单元格结构 -->
    <div class="info">
      <h3>{{ info.name }}</h3>
      <p>{{ info.goods_brief }}</p>
      <div>{{ info.retail_price | filterMoney }}</div>
    </div>
    <van-cell
      class="showSku"
      title="展示弹出层"
      is-link
      @click="isSkuShow = true"
    />

    <Products :goodsList="goodsList" />
    <van-sku
      v-model="isSkuShow"
      ref="sku"
      :sku="sku"
      :goods="goods"
      :hide-stock="sku.hide_stock"
    />
    <!-- 产品参数 -->
    <div class="attribute">
      <h4>商品参数</h4>
      <ul>
        <li v-for="(item, index) in attribute" :key="index">
          <span>{{ item.name }}</span>
          <section class="van-ellipsis">{{ item.value }}</section>
        </li>
      </ul>
    </div>
    <!-- 产品详情图片 -->
    <div class="box" ref="box"></div>
    <!-- 标题结构样式 -->
    <div class="title">
      <span>常见问题</span>
    </div>
    <ul class="issue">
      <li v-for="item in issue" :key="item.id">
        <h3>{{ item.question }}</h3>
        <p>{{ item.answer }}</p>
      </li>
    </ul>
    <AppGoodAction @addToCart="addToCart" :badge="badge"></AppGoodAction>
  </div>
</template>

<script>
import Tips from "@/components/Tips"
// AppGoodAction Products
import AppGoodAction from "@/components/AppGoodAction.vue"
import Products from "@/components/product.vue"
import { GetGoodsDetailData, GetCartNum, AddToCart } from "@/request/api"
export default {
  data() {
    return {
      isSkuShow: false,
      gallery: [],
      info: {},
      attribute: [],
      issue: [],
      goodsList: [],
      sku: {
        tree: [],
        hide_stock: false,
        price: "",
        stock_num: 0
      },
      goods: {
        picture: ""
      },
      badge: 0,
      // productList
      productList: []
    }
  },
  created() {
    // console.log(this.$route.query.id);
    GetGoodsDetailData({
      id: this.$route.query.id
    }).then(res => {
      // console.log(res);
      if (res.errno == 0) {
        // console.log(res.data);
        let { gallery, info, attribute, issue, goodsList, productList } = res.data
        this.gallery = gallery
        this.info = info;

        //获取产品参数
        this.attribute = attribute;
        // 将图片渲染到box中
        this.$refs.box.innerHTML = info.goods_desc;
        //  常见问题
        this.issue = issue
        // 详情页中产品列表组件的复用
        this.goodsList = goodsList
        // sku展示图 list_pic_url
        this.goods.picture = info.list_pic_url
        // sku库存 goods_number
        this.sku.stock_num = info.goods_number
        //sku 库存价格
        this.sku.price = info.retail_price
        //  获取productList数组
        this.productList = productList
      }
    }),
      GetCartNum().then(res => {
        if (res.errno == 0) {
          this.badge = res.data.cartTotal.goodsCount
        }
      })
  },
  methods: {
    addToCart() {
      // console.log(this.$refs)
      if (this.isSkuShow) {
        // 加入购物车
        /* 1、获取商品购买数量*/
        // console.log(this.$refs.sku.getSkuData().selectedNum);

        /* 2、发送请求，添加到购物车 */
        AddToCart({
          goodsId: this.$route.query.id,   // 地址栏id
          productId: this.productList[0].id,   //产品id，在刷新页面时的数据对象下获取productList数组
          number: this.$refs.sku.getSkuData().selectedNum,

        }).then(res => {
          /* 1、更新购物车数量 */


          this.badge = res.data.cartTotal.goodsCount

          // console.log(this.productList)
          /* 2、跳转到购物车页面 */
          this.$toast.success("添加成功")

          setTimeout(() => {
            this.$router.push("/cart")
          }, 1500)
        })

      } else {
        this.isSkuShow = true
      }
    }
  },
  components: {
    Tips,
    AppGoodAction,
    Products

  }
}
</script>

